<template>
  <div id="header" v-if="isHeader">
    <div class="header-logo">
      <router-link
        v-if="headerPathName !== constants.URL_TYPE.MAIN.MAIN"
        :to="{ name: constants.URL_TYPE.MAIN.MAIN }"
      >
        <img class="small-logo" src="/img/small_home_logo.png" />
      </router-link>
      <router-link
        v-if="headerPathName !== constants.URL_TYPE.MAIN.MAIN"
        :to="{ name: headerPathName, params: { id: headerPathParams } }"
      >
        <div class="header-name">{{ headerTitle }}</div>
      </router-link>

      <router-link v-else :to="{ name: headerPathName, params: { id: headerPathParams } }">
        <img class="home-logo" src="/img/home_logo.png" />
      </router-link>
    </div>
    <div class="right d-flex">
      <select class="select-box" v-model="selected">
        <option v-for="option in options" :value="option.value" :key="option.text">{{ option.text }}</option>
      </select>
      <div class="search-input">
        <i class="fas fa-search"></i>
        <input v-model="keyword" type="text" @keyup.enter="search" />
      </div>

      <router-link
        v-if="!isLogined"
        :to="{ name: constants.URL_TYPE.USER.LOGIN }"
        class="login-btn"
      ></router-link>

      <router-link v-if="isLogined" to="/create" class="login-btn"> </router-link>
      <div class="dropdown-box" v-if="isLogined">
        <a
          class="nav-link dropdown-toggle"
          href="#"
          id="navbarDropdown"
          role="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          <i class="far fa-smile"></i>
        </a>
        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
          <router-link
            class="dropdown-item"
            :to="{
              name: constants.URL_TYPE.POST.POSTITEMS,
              params: { id: username },
            }"
          >  </router-link>
          <router-link class="dropdown-item" :to="{ name: constants.URL_TYPE.USER.LOGOUT }"></router-link>
          <div class="dropdown-divider"></div>
          <router-link
            class="dropdown-item"
            :to="{
            name : constants.URL_TYPE.USER.PROFILECONFIG,
            params : {
              username : username
            }
            }"
          > </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import constants from "../../lib/constants";
import { mapState, mapGetters } from "vuex";
export default {
  name: "Header",
  components: {},
  props: ["isHeader", "isLogined"],
  computed: {
    ...mapState([
      "headerTitle",
      "headerPathName",
      "headerPathParams",
      "username",
    ]),
  },
  watch: {},
  created() {},
  methods: {
    search() {
      this.$router.push({
        name: constants.URL_TYPE.MAIN.SEARCH,
        query: {
          type: this.selected,
          search: this.keyword,
        },
      });
    },
  },
  data: function () {
    return {
      constants,
      keyword: "",
      selected: "title",
      options: [
        { text: "", value: "title" },
        { text: "", value: "content" },
        { text: "+", value: "text" },
        { text: "", value: "tag" },
      ],
    };
  },
};
</script>

<style scoped>
#header {
  height: 10vh;
}
#header > h1 {
  font-size: 30px;
}
.search-input {
  margin: 7px;
}
</style>
