<template>
  <div class="user" id="join">
    <div class="wrapC table">
      <div class="middle">
        <h1></h1>
        <div class="form-wrap">
          <div class="input-wrap">
            <input v-model="nickName" id="nickname" placeholder=" " type="text" />
          </div>

          <div class="input-wrap">
            <input v-model="email" id="email" placeholder=" " type="text" />
          </div>

          <div class="input-wrap password-wrap">
            <input v-model="password" id="password" :type="passwordType" placeholder=" " />
            <span :class="{active : passwordType==='text'}">
              <i class="fas fa-eye"></i>
            </span>
          </div>

          <div class="input-wrap password-wrap">
            <input
              v-model="passwordConfirm"
              id="password-confirm"
              :type="passwordConfirmType"
              placeholder="   "
            />
            <span :class="{active : passwordConfirmType==='text'}">
              <i class="fas fa-eye"></i>
            </span>
          </div>
        </div>

        <label>
          <input v-model="isTerm" type="checkbox" id="term" />
          <span> </span>
        </label>

        <span class="go-term"> </span>

        <button class="btn" @click="signup">
          <span></span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/user.scss";
import constants from "../../lib/constants";
import axios from "axios";
export default {
  components: {},
  created() {},
  methods: {
    signup() {
      const signupData = {
        useremail: this.email,
        username: this.nickName,
        password: this.password,
      };
      if (this.checkSingupData(signupData)) {
        axios
          .post(constants.baseUrl + "user/signup", signupData)
          .then(() => {
            this.alertAfterpush();
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    alertAfterpush() {
      alert(
        " .   Login  ."
      );
      this.$router.push({ name: constants.URL_TYPE.USER.LOGIN });
      window.close();
    },
    checkSingupData(signupData) {
      if (this.isTerm) {
        console.log(signupData);
        if (signupData.password === this.passwordConfirm) {
          if (signupData.password.length >= 8) {
            console.log(this.reg.test(signupData.useremail), this.reg);
            if (
              signupData.email !== "" &&
              this.reg.test(signupData.useremail)
            ) {
              if (signupData.username.length >= 3) {
                return true;
              } else {
                alert(" 3  ");
              }
            } else {
              alert("  .");
            }
          } else {
            alert("  .");
          }
        } else {
          alert("    .");
        }
      } else {
        alert("  ");
      }
    },
  },
  watch: {},
  data: () => {
    return {
      constants,
      email: "",
      nickName: "",
      password: "",
      passwordConfirm: "",
      isTerm: false,
      passwordType: "password",
      passwordConfirmType: "password",
      reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
    };
  },
};
</script>


