<template>
  <div id="create" class="container">
    <div class="row">
      <div class="col-12 p-0" style="text-align:left; margin:5vh 0px 3vh 0px;">
        <p id="newPost">  </p>
      </div>

      <input placeholder=" " class="titleInput" v-model="title" type="text" />

      <div id="tag" class="flex-column">
        <button class="tagBtn" v-for="tag in this.tagList" :key="tag">{{tag}}</button>
      </div>
      <input
        placeholder="Tag  Enter "
        @keyup.enter="tagEvent"
        @keyup.delete="deleteTag"
        class="taginput"
        v-model="inputTag"
        type="text"
      />
      <v-md-editor
        style="margin:5vh 0px; box-sizing: border-box;"
        id="create"
        placeholder="   "
        v-model="text"
        left-toolbar="undo redo | clear h hr italic bold ol ul quote table strikethrough image code"
        :disabled-menus="[]"
        @upload-image="handleUploadImage"
        @copy-code-success="handleCopyCodeSuccess"
      />
      <button @click="checkCreate"></button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import constants from "@/lib/constants.js";
import { mapState } from "vuex";
export default {
  name: "CreateView",
  data() {
    return {
      constants,
      title: "",
      text: "",
      inputTag: null,
      tagList: [],
      imageList: [],
    };
  },
  computed: {
    ...mapState(["authToken"]),
  },
  methods: {
    checkCreate() {
      if (confirm("?")) {
        this.createPost();
      }
    },
    // Tag  
    tagEvent() {
      if (this.inputTag.length <= 15 && !!this.inputTag.trim().length) {
        if (!this.tagList.includes(this.inputTag)) {
          this.tagList.push(this.inputTag.trim());
          this.inputTag = null;
        } else {
          this.inputTag = null;
          alert("  Tag .");
        }
      } else {
        if (this.inputTag.length > 15) {
          alert("Tag 15  ");
        } else {
          alert("Tag .");
        }
        this.inputTag = null;
      }
    },

    /// Tag  
    deleteTag() {
      if (
        this.tagList.length >= 1 &&
        (this.inputTag === null || this.inputTag === "")
      ) {
        if (
          confirm(
            this.tagList[this.tagList.length - 1] +
              " Tag" +
              " ?"
          )
        ) {
          this.tagList.pop();
        }
      }
    },

    ///  
    handleCopyCodeSuccess(code) {
      alert(" .");
      console.log(code);
    },
    changeHeight() {
      const mainMarkdownEditor = document.querySelector(".v-md-editor__main");
      mainMarkdownEditor.style.minHeight = "55vh";
    },
    createPost() {
      const configTag = "," + this.tagList.join(",") + ",";
      const postData = {
        title: this.title,
        content: this.text,
        tags: configTag,
      };
      var titleCheck = this.title;
      var contentCheck = this.text;
      if (!titleCheck.trim().length || !contentCheck.trim().length) {
        alert("    .");
      } else {
        this.imageList = this.imageList.filter((item) => {
          return this.text.includes(item.iid);
        });
        const images = this.imageList.map((image) => {
          const data = {};
          data.image = image.iid;
          return data;
        });

        const totalData = {
          post: [postData],
          images: images,
          token: this.authToken,
        };
        axios
          .post(
            constants.baseUrl + `post/${this.$store.state.username}/`,
            totalData
          )
          .then((res) => {
            this.$router.push({
              name: constants.URL_TYPE.POST.POST,
              params: {
                id: this.$store.state.username,
                pid: res.data.object.pid,
              },
            });
          })
          .catch((err) => {
            console.log(err.response);
          });
      }
    },
    handleUploadImage(event, insertImage, files) {
      const formData = new FormData();
      formData.append("files", files[0]);

      axios
        .post(constants.baseUrl + "file/upload/", formData)
        .then((res) => {
          console.log(res.data);
          this.imageList.push(res.data.object);
          insertImage({
            url: `${constants.baseUrl}${res.data.object.path}`,
            desc: res.data.object.iname,
          });
        })
        .catch((err) => {
          console.log(err.response);
        });
    },
  },
  mounted() {
    this.changeHeight();
  },
};
</script>

<style scoped>
#newPost {
  font-size: 40px;
  border-bottom: #ddd solid 4px;
  display: inline;
}
.titleInput {
  font-size: 30px;
  width: 100%;
  margin-bottom: 17px;
}
.taginput {
  font-size: 17px;
  padding-bottom: 4px;
  padding-left: 6px;
  width: 30%;
  border-bottom: 1px solid;
  margin: 1vh 5px;
}
.tagBtn {
  background-color: #e8eaf6;
  border: none;
  cursor: auto;
  border-radius: 16px;
  padding: 7px;
  margin: 7px 3px;
  color: #0ca678;
}
</style>
