        <div class="filebox">
          <label for="ex_file"> </label>
          <input type="file" id="ex_file" ref="profileimg" @change="updateprofile" />
        </div>
        <button class="button-delete" @click="deleteprofile"> </button>
      </div>
      <div class="user-intro">
        <div class="user-name">{{username}}</div>
        <textarea class="intro-content"></textarea>
        <button></button>
      </div>
    </div>
<!--    <div class="user-active">-->
